.slider {
  position: relative;
}

.slider__slide-img-wrap {
  margin-bottom: 7px;

  @media (max-width: $mobile) {
    margin-bottom: 2px;
  }
}

.slider__slide-img-wrap img {
  display: flex;

  @media (max-width: $desktop) {
    width: 324px;
    height: auto;
  }

  @media (max-width: $mobile) {
    width: 100%;
    height: auto;
  }
}

.slider .swiper-container {
  position: unset;
}

.slider__container .swiper-slide {
  max-width: 270px;

  @media (max-width: $desktop) {
    max-width: 324px;
  }

  @media (max-width: $mobile) {
    max-width: unset;
  }
}

.slider__slide a {
  display: inline-block;
  vertical-align: middle;

  @media (max-width: $mobile) {
    width: 100%;
  }
}

.slider__slide-title-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slider__slide-title-wrap h3 {
  font-size: 16px;
  line-height: 130%;
  color: #1d1613;

  text-align: center;
  margin-bottom: 5px;
}

.slider__slide-title-wrap span {
  font-weight: bold;
  font-size: 14px;
  line-height: 130%;
  color: #1d1613;
}

.slider__container .swiper-pagination {
  bottom: -76px;

  @media (max-width: $desktop) {
    bottom: -66px;
  }

  @media (max-width: $mobile) {
    bottom: -55px;
  }
}

.slider__container .swiper-pagination-bullet {
  width: 13px;
  height: auto;

  background-color: transparent;

  @media (max-width: $desktop) {
    width: 14px;
  }
}

.slider__container .swiper-button-prev,
.slider__container .swiper-button-next {
  position: absolute;
  top: 50px;
  width: 30px;

  @media (max-width: $desktop) {
    width: 36px;
  }

  @media (max-width: $mobile) {
    display: none;
  }
}

.slider__container .swiper-button-prev {
  left: 0;
}

.slider__container .swiper-button-next {
  right: 0;
}

.slider__container .swiper-button-prev::after,
.slider__container .swiper-button-next::after {
  content: "none";
}

.slider__button-prev svg,
.slider__button-next svg {
  @media (max-width: $desktop) {
    width: 36px;
    height: 15px;
  }
}

.slider-block {
  @media (max-width: $desktop - 1) {
    margin-bottom: 57px;
  }

  @media (max-width: $mobile) {
    margin-bottom: 48px;
  }
}
