html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  font-family: "Poppins", "Tahoma", "Geneva", sans-serif;
  font-style: normal;
  font-weight: 500;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Cormorant Garamond"), url("../fonts/cormorantgaramond-regular.woff2") format("woff2"), url("../fonts/cormorantgaramond-regular.woff") format("woff");
}
@font-face {
  font-family: "Cormorant Garamond Italic";
  font-style: italic;
  font-weight: 400;
